.product-detail {
    display: flex;
    align-items: flex-start;
    justify-content: space-between;
    padding: 20px;
    border-bottom: 1px solid #ccc; /* 회색선 추가 */
}

.product-image-container {
    flex: 1;
    max-width: 50%;
}

.product-image-container img {
    width: 100%;
    height: auto;
    border-radius: 10px;
}

.product-info {
    flex: 1;
    padding-left: 20px;
}

.best-badge {
    display: block;
    margin: 10px 0;
    width: 50px; /* 이미지를 적절한 크기로 조정 */
    height: auto;
}

.product-info h3 {
    color: gray;
    margin-bottom: 10px;
}

.product-info h1 {
    margin-bottom: 10px;
}

.product-info p {
    font-size: 18px;
    margin-bottom: 8px;
}


.product-tabs {
    display: flex;
    justify-content: center; /* 탭을 중앙 정렬 */
    margin-top: 20px;
    border-top: 1px solid #e0e0e0; /* 상단 회색 선을 더 얇게 */
}

.product-tabs button {
    padding: 10px 20px;
    margin: 0 20px; /* 버튼 사이 간격 추가 */
    background-color: transparent; /* 배경을 투명하게 */
    border: none;
    cursor: pointer;
    font-size: 16px;
    color: #999; /* 기본 글자 색을 회색으로 */
    position: relative;
}

.product-tabs button:not(:last-child)::after {
    content: "";
    position: absolute;
    right: -10px; /* 구분선을 버튼 사이에 넣기 위해 위치 설정 */
    top: 50%;
    transform: translateY(-50%);
    height: 18px; /* 선의 높이 */
    width: 1px; /* 구분선의 두께 */
    background-color: #ccc; /* 구분선 색상 */
}

.product-tabs button.active {
    font-weight: bold;
    color: black; /* 활성화된 버튼은 검정색 */
}



.product-content {
    margin-top: 20px;
    border-bottom: 1px solid #ccc; /* 회색선 추가 */
}

.product-content p {
    font-size: 14px;
}

.product-pricing {
    margin: 20px 0;
    border-bottom: 1px solid #ccc; /* 회색선 추가 */
}

.general-price, .vip-price {
    display: flex;
    justify-content: space-between; /* 텍스트와 숫자를 양쪽 끝으로 정렬 */
    font-size: 18px;
    margin-bottom: 10px;
}

.original-price {
    margin-right: 10px;
}

.original-price1{
    font-size: 24px;
}

.discounted-price {
    font-weight: bold;
}

.vip-price {
    display: flex;
    align-items: baseline;
    margin-bottom: 10px;
}

.vip-discount {
    font-weight: bold;
    margin-right: 10px;
}

.vip-discount {
    color: #ae1522;
    font-weight: bold;
    font-size: 20px;
    margin-right: 10px;
}

.vip-discounted-price {
    font-size: 24px;
    font-weight: bold;
    color: #ae1522;
}

.vip-notice {
    background-color: #dbe8fa;
    padding: 10px;
    margin-bottom: 20px;
    border-bottom: 1px solid #ccc; /* 회색선 추가 */
}

.vip-notice a {
    color: black;
    text-decoration: none;
}

.option-select {
    margin-bottom: 10px;
    border-bottom: 1px solid #ccc; /* 회색선 추가 */
}

.option-select select {
    padding: 10px;
    width: 100%;
    font-size: 16px;
    border: 1px solid #ccc;
    border-radius: 4px;
}

.notice {
    color: red;
    font-size: 12px;
    margin-top: 10px;
}


.selected-product-container {
    margin-top: 20px;
    padding: 20px;
    border: 1px solid #ccc;
    border-radius: 5px;
}

.selected-product-details {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
}

.selected-product-info {
    font-size: 16px;
    color: #333;
}

.total-price {
    font-weight: bold;
    font-size: 18px;
    color: #333;
}

.total-price-section {
    display: flex;
    justify-content: space-between;
    margin-bottom: 20px;
    font-size: 18px;
}

.product-buttons {
    display: flex;
    justify-content: space-between;
}

.cart-button, .buy-button {
    padding: 10px 20px;
    font-size: 16px;
    border: none;
    cursor: pointer;
    border-radius: 5px;
}

.cart-button {
    background-color: white;
    color: #333;
    border: 1px solid #ccc;
}

.buy-button {
    background-color: #D3EE98;
    color: black;
}
