.vip {
    background-image: linear-gradient(-225deg, #fffff2 0%, #cbe593 100%);
}

.vip-container {
    padding: 200px;
    text-align: center;
    overflow: hidden; /* 텍스트가 밖으로 벗어나지 않도록 */
}

.vip-container h1, .vip-container p, .vip-container ul li,
.benepit-container h1 { /* benepit-container에도 동일한 애니메이션 적용 */
    opacity: 0; /* 처음엔 보이지 않도록 설정 */
    transform: translateY(100px); /* 아래에서 시작 */
    animation: slideUp 1s forwards; /* 애니메이션을 1초 동안 진행 */
}

/* 애니메이션: 아래에서 위로 올라오게 */
@keyframes slideUp {
    to {
        opacity: 1;
        transform: translateY(0); /* 원래 위치로 돌아옴 */
    }
}

/* 각 텍스트에 개별 애니메이션 딜레이를 적용하여 순차적으로 등장 */
.vip-container h1 {
    animation-delay: 0.2s;
}

.vip-container p {
    animation-delay: 0.4s;
}

.vip-container ul li:nth-child(1) {
    animation-delay: 0.6s;
}

.vip-container ul li:nth-child(2) {
    animation-delay: 0.8s;
}

.vip-container ul li:nth-child(3) {
    animation-delay: 1s;
}

/* benepit-container에 애니메이션 딜레이 추가 */
.benepit-container h1 {
    animation-delay: 1.2s; /* VIP 섹션 이후에 등장 */
}

.vip-container h1 {
    font-size: 50px;
    margin-bottom: 20px;
}

.vip-container p {
    font-size: 16px;
    margin-bottom: 10px;
    font-weight: bold;
}

.vip-container ul {
    list-style-type: none;
    padding: 0;
}

.vip-container li {
    font-size: 14px;
    margin-bottom: 8px;
    font-weight: bold;
}

.benepit-container {
    text-align: center;
    overflow: hidden; /* 텍스트가 밖으로 벗어나지 않도록 */
    padding: 50px 0; /* 적절한 패딩 추가 */
}

.benepit-container h1 {
    font-size: 50px;
    margin-bottom: 20px;
}
