.mypage-container {
    display: flex;
    justify-content: center;
    align-items: center;
    margin: 20px;
    padding: 20px;
}

.profile-image-container {
    position: relative;
    width: 120px;
    height: 120px;
    margin: 20px auto;
    text-align: center;
}

.profile-image {
    width: 100%;
    height: 100%;
    border-radius: 50%;
    object-fit: cover;
    border: 2px solid #ddd;
}

.save-button-container {
    text-align: right;  /* 버튼을 오른쪽으로 정렬 */
    margin-top: 10px;
}

.user-profile-info{
    margin-top: 60px;
}


.mypage-sidebar {
    width: 400px; 
    min-height: 600px; 
    border-right: 1px solid #ddd;
    padding: 20px; /* 패딩을 늘려 전체적으로 크기를 키움 */
    margin-right: 20px;
    background-color: #ffffff;
    box-shadow: 4px 4px 10px rgba(0, 0, 0, 0.1); /* 오른쪽에 그림자 효과 추가 */
    border-radius: 10px; /* 모서리를 살짝 둥글게 */
}



.mypage-sidebar h3 {
    font-size: 22px; /* 타이틀 크기 증가 */
    margin-bottom: 25px;
}

.mypage-sidebar p {
    font-size: 18px; /* 텍스트 크기 증가 */
    margin-bottom: 10px;
}

.mypage-sidebar ul {
    list-style: none;
    padding: 0;
    margin-top: 30px; /* 프로필 사진과 리스트 사이에 간격 추가 */
}


.mypage-sidebar ul li {
    margin-bottom: 10px;
}

.mypage-sidebar ul li a {
    font-size: 19px;
    text-decoration: none;
    color: #333;
}


.mypage-content {
    width: 75%;
    margin-top: -300px; /* 컨테이너를 위로 이동 */
    display: flex;
    flex-direction: column; 
    justify-content: center;
    align-items: flex-start;
}

.mypage-content p {
    font-weight: bold;
    color:grey;
}

.mypage-card {
    border: 1px solid #D3EE98;
    background-color: #f9f9f9;
    border-radius: 10px;
    padding: 20px;
    width: 600px; 
    box-shadow: 0px 4px 6px rgba(0, 0, 0, 0.1);
    margin-left: 200px;
}

.mypage-header {
    border-bottom: 1px solid #ddd;
    padding-bottom: 10px;
    margin-bottom: 20px;
}

.mypage-header p {
    color: grey; /* 기본정보 글씨 색상을 회색으로 설정 */
    font-weight: bold; /* 기본정보 글씨를 굵게 설정 */
}

.mypage-body {
    display: flex;
    flex-direction: column;
}

.info-row {
    display: flex;
    justify-content: space-between;
    align-items: center;
    margin-bottom: 10px;
}

.info-row label {
    color: black;
    font-weight: bold;
}


.info-row p {
    color: black;
    flex-grow: 1;
    margin-left: 10px;
    text-align: left;
}


