@font-face {
    font-family: 'Godo';
    font-style: normal;
    font-weight: 400;
    src: url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff2') format('woff2'), url('//fastly.jsdelivr.net/korean-webfonts/1/corps/godo/Godo/GodoM.woff') format('woff');
}

.animal-detail-page h1 {
    text-align: center; /* h1 가운데 정렬 */
    font-family: 'Godo', sans-serif;
    font-weight: 300;
    margin: 20px;
}

.animal-detail-page {
    margin: 20px;
}

.animal-images {
    display: flex;
    justify-content: center;
    margin-bottom: 20px;
}

.animal-images img {
    width: 500px; /* 원하는 너비로 설정 */
    height: 400px; /* 높이도 고정 크기로 설정 */
    object-fit: cover; /* 이미지 비율을 유지하면서 영역을 채움 */
    max-width: 100%; /* 부모 요소의 최대 크기를 넘지 않도록 설정 */
    max-height: 100%; /* 최대 높이 제한 */
}


.animal-info-table {
    width: 100%;
    border-collapse: collapse;
    margin: 20px 0;
    font-size: 16px;
}

.animal-info-table th,
.animal-info-table td {
    font-family: 'Godo', sans-serif;
    border: 1px solid #ddd;
    padding: 12px;
    text-align: left;
}

.animal-info-table th {
    background-color: #666; /* 좌측 셀의 배경색 설정 */
    color: white; /* 텍스트 색상 */
    font-weight: 300;
    width: 20%; /* 열 너비 조정 */
}

.animal-info-table td {
    background-color: #f4f4f4;
    color: black;
    width: 30%; /* 나머지 셀 너비 */
}
